<template>
  <u-popup v-model="localShow" mode="center" border-radius="20" :custom-style="{ backgroundColor: 'rgba(0, 0, 0, 0.7)' }">
    <view class="prizes-box">
      <view class="prizes-header">
        <u-icon class="close-icon" name="close" color="#333" size="38" @click="close"></u-icon>
        <view class="prizes-title">我的奖品</view>
      </view>
      <view class="prizes-content">
        <scroll-view scroll-y class="prize-scroll">
          <view class="prize-list" v-if="listData.length">
            <view class="prize-item" v-for="(prize, index) in listData" :key="index">
              <image :src="prize.thumb" mode="aspectFit" class="prize-image"></image>
              <view class="prize-info">
                <view class="prize-name">{{ prize.title }}</view>
                <view class="prize-time">X{{ prize.num }}</view>
              </view>
              <button class="prize-btn" @click="claimPrize(prize)">提货</button>
            </view>
          </view>
          <template v-else>
            <u-empty text="暂无奖品数据" mode="list"></u-empty>
          </template>
        </scroll-view>
      </view>
    </view>
  </u-popup>
</template>

<script>
export default {
  name: 'MyPrizesDialog',
  props: {
    show: {
      type: Boolean,
      default: false
    },
    prizes: {
      type: Array,
      default: () => []
    }
  },
  data () {
    return {
      localShow: this.show,
      listData: []
    }
  },
  watch: {
    show (val) {
      this.localShow = val
    },
    localShow (val) {
      this.$emit('update:show', val)
      if (val) {
        this.getList()  // 弹窗打开时调用接口
      }
    }
  },
  methods: {
    getList () {
      this.req({
        url: '/v1/user/award',
        data: {
          status: 0,
          mark_id: 0
        },
        Loading: true,
        success: res => {
          if (res.code == 200) {
            let newArr = res.data.map(item => {
              return { ...item, checked: false, num: item.total }
            })

            this.listData = newArr
          } else {
          }
        }
      })
    },
    close () {
      this.localShow = false
    },
    claimPrize (prize) {
      // this.$emit('claim', prize)
      console.log(prize)
      this.$common.to({
        url: '/package/mine/bag-send',
        query: {
          goods_id: prize.goods_id,
          num: prize.num
        }
      })
      this.localShow = false
    }
  }
}
</script>


<style lang="scss" scoped>
.rules-box,
.prizes-box,
.weapon-box {
  width: 650rpx;
  background: linear-gradient(180deg, #FFFFFF 0%, #F8F8F8 100%);
  border-radius: 30rpx;
  padding: 40rpx;

  .rules-header,
  .prizes-header,
  .weapon-header {
    position: relative;
    text-align: center;
    margin-bottom: 40rpx;

    .close-icon {
      position: absolute;
      right: 0;
      top: 50%;
      transform: translateY(-50%);
      width: 40rpx;
      height: 40rpx;
    }

    .rules-title,
    .prizes-title,
    .weapon-title {
      font-size: 36rpx;
      font-weight: bold;
      color: #333;
      background: linear-gradient(90deg, #FF6B6B 0%, #FF8E8E 100%);
      -webkit-background-clip: text;
      -webkit-text-fill-color: transparent;
    }
  }
}

.prize-list {
  .prize-item {
    display: flex;
    align-items: center;
    padding: 20rpx;
    background: #FFFFFF;
    border-radius: 16rpx;
    margin-bottom: 20rpx;
    box-shadow: 0 4rpx 12rpx rgba(0, 0, 0, 0.05);

    .prize-image {
      width: 100rpx;
      height: 100rpx;
      margin-right: 20rpx;
    }

    .prize-info {
      flex: 1;

      .prize-name {
        font-size: 28rpx;
        font-weight: bold;
        color: #333;
        margin-bottom: 10rpx;
        display: -webkit-box;
        -webkit-box-orient: vertical;
        -webkit-line-clamp: 2;
        overflow: hidden;
      }

      .prize-time {
        font-size: 24rpx;
        color: #999;
      }
    }

    .prize-btn {
      background: #FF6B6B;
      color: #fff;
      font-size: 24rpx;
      padding: 10rpx 30rpx;
      border-radius: 20rpx;
      margin-left: 20rpx;
      flex-shrink: 0;
    }
  }
}

.prize-scroll {
  max-height: 800rpx; // 根据弹窗实际大小调整
  overflow: auto;
}
</style>
